
import optionService from '../service/optionService'
import saleReportService from '../service/saleRepotService'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Loading from './loading'

const FILTER_OPTION = 'ROSE_FILER_OPTION'; 
const PROJ_LIST = 'ROSE_PROJ_LIST'; 
export default {
components: {
Group,
XButton,
PopupPicker,
Loading
},
data() {
return {
region: [],
whichIndex: '',
showMore: false,
pickerList: [
{
title: '所在地区',
ph: '请选择地区',
value: [],
list: []
},
{
title: '所在银行',
ph: '请选择银行',
value: [],
list: []
},
{
title: '所属区域',
ph: '请选择区域',
value: [],
list: []
},
{
title: '项目产品',
ph: '请选择项目',
value: [],
list: []
}
],
regionList: [],
bankList: [],
deptList: [],
projList: [],
showLoading: false,
}
},
computed: {
regionTitle() {
return this.region[0] ? this.region[0].split('-')[1] + '地区' : '报表'
}
},
methods: {

getRegion(params = {}) {
return new Promise((resolve, reject) => {
if (this.regionList.length > 0) {
resolve(this.regionList)
} else {
optionService.getRegion().then(data => {
let region = data && data.reduce((arr, item) => {
arr.push(item.name);
return arr
}, ['']);
this.regionList = region;
resolve(region)
}).catch(err => {
this.$vux.alert.show({
content: err.message
})
})
}
})
},

getBank(params = {}) {
return new Promise((resolve, reject) => {
if (this.bankList.length > 0) {
resolve(this.bankList)
} else {
optionService.getBank().then(data => {
let bank = data && data.reduce((arr, item) => {
arr.push(item.name);
return arr
}, ['']);
this.bankList = bank;
resolve(bank)
}).catch(err => {
this.$vux.alert.show({
content: err.message
})
})
}
})
},

getDept(params = {}) {
return new Promise((resolve, reject) => {
if (this.deptList.length > 0) {
resolve(this.deptList)
} else {
optionService.getDept().then(data => {
let dept = data.tableContent && data.tableContent.reduce((arr, item) => {
arr.push(item.groupName);
return arr
}, ['']);
this.deptList = dept;
resolve(dept)
}).catch(err => {
this.$vux.alert.show({
content: err.message
})
})
}
})
},

getProj() {
return new Promise((resolve, reject) => {
if (this.projList.length > 0) {
resolve(this.projList)
} else {
saleReportService.saleRepotList().then(data => {
let proj = data.tableContent.reduce((arr, item) => {
arr.push(item['trans_detail_uncalc.transObjCode']);
return arr
}, ['']);
this.projList = proj;
sessionStorage.setItem(PROJ_LIST, JSON.stringify(proj));
resolve(proj)
}).catch(err => {
this.$vux.alert.show({
content: err.message
})
})
}
})
},
getPickerValue(index, value) {
if (!value[0]) {
this.pickerList[index].value = []
}
this.showMore = true;
this.whichIndex = index;
if (index === 0) {
this.region = value;
} else if (index === 2) {
this.showMore = false;
}
},
createNew() {

let index = this.whichIndex;
if (index === 0) {
this.getBank().then(bank => {
this.pickerList.push({
title: '所在银行',
ph: '请选择银行',
value: [],
list: [bank]
})
});
this.showMore = false;
} else if (index === 1) {
this.getDept().then(dept => {
this.pickerList.push({
title: '所属区域',
ph: '请选择区域',
value: [],
list: [dept]
})
});
this.showMore = false;
}
},

goRp() {
let filterParams = {};
this.pickerList.forEach((item, index) => {
switch (index) {
case 0: // 地区
filterParams.region = encodeURI(item.value[0] || '');
break;
case 1: // 银行
filterParams.bank = encodeURI(item.value[0] || '');
break;
case 2: // 部门、区域
filterParams.dept = encodeURI(item.value[0] || '');
break;
case 3: // 项目
filterParams.proj = encodeURI(item.value[0] || '');
break;
}
});
/*if (!filterParams.region && !filterParams.dept && !filterParams.bank && !filterParams.proj) {
this.$vux.alert.show({
content: '请至少选择一个条件'
});
return
}*/
sessionStorage.setItem(FILTER_OPTION, JSON.stringify(this.pickerList))
this.$router.push({
path: '/reportsOp/reports',
query: filterParams
})
},
},
created() {
(async () => {
let filter = JSON.parse(sessionStorage.getItem(FILTER_OPTION));
this.showLoading = true;
if (!filter) {
let tmpPickerList = [];
await this.getRegion().then(region => {
tmpPickerList.push({
title: '所在地区',
ph: '请选择地区',
value: [],
list: [region]
})
});
await this.getBank().then(bank => {
tmpPickerList.push({
title: '所在银行',
ph: '请选择银行',
value: [],
list: [bank]
})
});
await this.getDept().then(dept => {
tmpPickerList.push({
title: '所属区域',
ph: '请选择区域',
value: [],
list: [dept]
})
});
await this.getProj().then(proj => {
tmpPickerList.push({
title: '项目产品',
ph: '请选择项目',
value: [],
list: [proj]
})
});
this.pickerList = tmpPickerList
} else {
/*await this.getRegion();
await this.getDept();
await this.getBank();
await this.getProj();*/
this.pickerList = filter
}
this.showLoading = false;
})()
},
destroyed() {
sessionStorage.removeItem(FILTER_OPTION)
}
}



