export default {
  name: "page_controller",
  props: {
    disabledPrev: { // 是否禁止点击上一页
      type: Boolean,
      default: true
    },
    disabledNext: { // 是否禁止点击下一页
      type: Boolean,
      default: true
    },
    page: { // 显示的页码
      type: Number,
      default: 1
    }
  },
  methods: {
    // TODO 点击上一页
    pagePrev() {
      if (this.disabledPrev) {
        return;
      }
      this.$emit('pageprev');
    },
    // TODO 点击下一页
    pageNext() {
      if (this.disabledNext) {
        return;
      }
      this.$emit('pagenext');
    },
  }
}



